body {
  font-family: 'Nunito';
  font-style: normal;
  background-color: #f9fbff;
}

.btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #F9FBFF;
  transition: all 0.4s ease-out;

  &:hover {
    background: #F9F5E8;
    color: #232C38;
  }
}

.button-burger {
  & span {
    display: block;
    height: 3px;
    width: 100%;
    background-color: #fff;

    &:nth-child(2), &:last-child {
      margin-top: 25%;
    }
  }

  @media screen and (min-width: 1024px) {
    display: none;
  }
}

.link {
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
}

.menu {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%);
  transition: transform 0.5s ease-out;

  &.show {
    transform: translate(0%);
  }

  @media screen and (min-width: 1024px) {
    position: absolute;
    transform: translate(0%);
  }
}

.menu-links {
  margin-bottom: 30px;
  opacity: 0.7;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  color: #EDF0F5;
}

.menu-links:hover, .menu-links.active {
  opacity: 1;
}

.slider-nav-button {
  font-size: 20px;
  width: 48px;
  height: 48px;
  background-color: #c7a17a;
  border-radius: 50%;
  color: #F9FBFF;

  &::after {
    display: none;
  }
}

.swiper-button-disabled {
  opacity: 0 !important;
}

.header-text, .contact-header, .cart-header {
  padding: 15px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 900;
  font-size: 26px;
  line-height: 32px;

  @media screen and (min-width: 390px) {
    font-size: 32px;
    line-height: 40px;
  }

  @media screen and (min-width: 768px) {
    font-size: 44px;
    line-height: 56px;
  }

  @media screen and (min-width: 1024px) {
    font-size: 54px;
    line-height: 70px;
  }

  @media screen and (min-width: 1920px) {
    font-size: 68px;
    line-height: 88px;
  }
}

.header-text.accent {
  color: #f9fbff;
  background-color: #c7a17a;

  & ~ .blank {
    background-color: #c7a17a;
  }
}

.header-text {
  background-color: #f9fbff;
  animation: show 1s ease-in-out 1s backwards;
}

.blank {
  background-color: #f9fbff;
  animation: show 1s ease-in-out 1s backwards, slideout 1s ease 1.8s forwards;
}

@keyframes show {
  from {
    transform: translateX(-130%);
  }

  to {
    transform: translateX(0%);
  }
}

@keyframes slideout {
  from {
    transform: translateX(0%);
  }

  to {
    transform: translateX(100%);
  }
}

.promo-video {
  background-image: url(/assets/images/promo_video.webp);
  background-size: auto 120%;
  background-repeat: no-repeat;
  background-position: top center;

  @media screen and (min-width: 882px) and (orientation: landscape) {
    background-size: auto;
  }

  @media screen and (min-width: 2100px) {
    background-size: cover;
  }
}

.play-button {
  margin: 80% 0 0 50%;
  transform: translate(-50%);
  width: 54px;
  height: 54px;
  background-color: #FFF;
  position: relative;
  border: none;
  border-radius: 50%;
  box-shadow: 0px 4px 24px rgba(74, 74, 74, 0.5);

  @media screen and (min-width: 568px) {
    margin-top: 50%;
  }

  @media screen and (min-width: 1920px) {
    width: 94px;
    height: 94px;
  }

  & svg {
    margin: 50%;
    transform: translate(-50%, -40%);

    @media screen and (min-width: 1920px) {
      width: 32px;
      height: 34px;
      
      margin: 50% 0 0 50%;
      transform: translate(-45%, -60%);
    }
  }
}

section {
  padding-top: 160px;
  padding-bottom: 128px;

  @media screen and (min-width: 1024px) {
    padding-top: 68px;
  }
}

section h2 {
  position: relative;
  text-transform: uppercase;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 900;
  font-size: 30px;
  line-height: 34px;

  & span {
    width: 100%;
    display: inline-block;
    text-transform: capitalize;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.features h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
}

.features p {
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
}

.features h3, .features p {
  color: #232C38;
}

.product-price {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  
  & span {
    display: inline-block;
    margin-left: 20px;
    vertical-align: bottom;
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
    text-decoration-line: line-through;
    color: #415167;
  }
}

.product-title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #232C38;
}

#cart .product-title {
  margin-bottom: 0;
}

.total-of-order {
  font-weight: 600;
  font-size: 32px;
  line-height: 40px;
}

.product-description {
  margin-bottom: 32px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  height: 66px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  color: #151D28;
}

.product-remove {
  font-size: 18px;
  line-height: 16px;
  color: #ff6347;
}

.product-card, .short-product-card {
  background-color: #FFF;
  transition: box-shadow 0.3s ease, opacity 0.3s ease;

  &:hover:not(.hide) {
    box-shadow: 0px 4px 24px #cdcdcd;
  }

  &.hide {
    opacity: 0.5;
  }
}

.coffee-combo {
  @media screen and (min-width: 882px){
    background: linear-gradient(90deg, rgba(249,251,255,1) 76.8%, rgba(65,81,103,1) 76.8%, rgba(65,81,103,1) 100%);
  }
}

.coffee-combo-products {
  padding: 20px 0;
  position: relative;
  overflow: hidden;
}

.giftset {
  background: rgb(237,240,245);
  background: linear-gradient(90deg, rgba(237,240,245,1) 76.8%, rgba(249,251,255,1) 76.8%, rgba(249,251,255,1) 100%);
}

.giftset-collections {
  position: relative;
}

.giftset-thumb {
  max-width: 234px;
  
  & img {
    @media screen and (min-width: 1024px) {
      transform: translate(-50%, 70%) scale(1.8);
    }
  }
}

.giftset-content {
  font-size: 0;

  @media screen and (min-width: 1024px) {
    max-width: 558px;
  }

  @media (min-width: 1536px) {
    max-width: 768px;
  }

  & .price {
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }

  & .title {
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #232C38;
  }

  & .description {
    margin-bottom: 24px;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
  }

  & .options {
    & * {
      @media screen and (min-width: 768px) {
        display: inline-block;
      }
    }

    & img {
      padding-right: 14px;
      vertical-align: bottom;
    }

    & .type {
      font-weight: 300;
      font-size: 16px;
      line-height: 22px;
    }

    & .value {
      display: block;
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
    }
  }

  & .btn {
    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }

  & .btn, .link {
    display: inline-block;
  }
}

.giftset-tabs {
  @media screen and (min-width: 1024px) {
    max-width: 90px;
  }

  & .tabs-menu {
    display: flex;
    flex-direction: row;
    position: static;

    @media screen and (min-width: 1024px) {
      flex-direction: column;
    }
  }

  & .tab {
    background-color: #B9C4DA;
    padding: 38px 45px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
    color: #415167;
    opacity: 0.4;
    cursor: pointer;
    transition: all 0.3s ease;

    @media screen and (min-width: 1024px) {
      padding: 65px 36px;
    }

    &.active, &:hover {
      opacity: 1;
      background-color: #fff;
    }
  }
}

.coffee {
  @media screen and (min-width: 882px){
    background: linear-gradient(90deg, rgba(65,81,103,1) 0%, rgba(65,81,103,1) 23%, rgba(249,251,255,1) 23%); 
  }
}

.coffee-products {
  position: relative;
  overflow: hidden;

  & .short-product {
    max-width: 235px;

    @media screen and (min-width: 882px) {
      max-width: 560px;
    }
  }

  & .short-product-card {
    flex-direction: column;
    margin-bottom: 32px;
    
    @media screen and (min-width: 882px) {
      flex-direction: row;
    }
  }

  & .product-thumb {
    max-width: 235px;
    max-height: 256px;
    background-color: #EDF0F5;
  }

  & .product-content {
    padding: 1rem;

    @media screen and (min-width: 1024px) {
      padding: 2.5rem;
    }
  }

  & .short-product-card {
    display: flex;
  }

  & .product-taste-description {
    margin-bottom: 18px;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
  }
}

.contact {
  background: linear-gradient(90deg, rgba(237,240,245,1) 76.8%, rgba(249,251,255,1) 76.8%, rgba(249,251,255,1) 100%);

  & h2 {
    color: #151D28;
  }

  & .contact-text {
    color: #0c213e;
    margin-bottom: 8px;
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;

    & span {
      display: block;
      font-weight: 600;
    }
  }
}